@import '../partials//variables';
@import '../common/mixin.scss';

.estimate {
    @include ptitle;
    border: 1px solid $border-color;
    border-radius: 2px;
    width: 72%;
    margin-top: 15px;

    .section1 ,.section2, .section3{
        padding: 20px;
        border-top: 1px solid $border-color;
        display: flex;
        flex-flow: wrap;
        .shippingtitle{
            width: 70%;
        }
        .shippingsummery{
            width: 30%;
            text-align: right;
            // display: flex;
        }
        .marginRight20{
            margin-right: 20px;
        }
    }
    .shipping1 {
        @include shipping;
        display: flex;
        width: 100%;
        
    }

    .shipping2 {
        @include shipping;
    }

    .shipping3 {
        @include shipping;
    }

    .shipping4 {
        @include summery;
        @include floatright;
    }

    .shipping5 {
        @include floatright;
        padding: 18.5px;
        font-size: 14px;
    }

    .downarrow1 {
        @include floatright;
        padding: 26.7px;
    }

    .downarrow2 {
        @include floatright;
        padding: 26.6px;
    }

    .downarrow3 {
        @include floatright;
        padding: 26.4px;
    }

}